export default "<b>തിരുവനന്തപുരം, തൃശൂർ, പാലക്കാട്, കോഴിക്കോട്, കണ്ണൂർ, കൊല്ലം, കൊച്ചി, ഗുരുവായൂർ</b> എന്നീ 7 നഗരങ്ങളിലെ ജലവിതരണം, മാലിന്യ ഓടകളുടെ ശൃംഖല, നഗര ഗതാഗതം തുടങ്ങിയ വികസന പ്രവർത്തനങ്ങൾ കേന്ദ്ര സർക്കാരിന്റെ ചിലവിൽ നടത്തുന്നതാണ് അമൃത് പദ്ധതി.<br><br>മൊത്തം പ്രോജക്ടുകൾ - <b>1008</b><br>നിർമ്മാണം പൂർത്തിയായത് - <b>638 (63%)</b><br>അനുവദിച്ച തുക - <b>1927.25 കോടി രൂപ</b><br><br>- അമൃത് കേരള വെബ്സൈറ് (2021 March 12 തീയതിയിലെ കണക്കുകൾ)<br><a href=\https://amrutkerala.org/state-level-progress\>https://amrutkerala.org/state-level-progress</a>"
